body {
    background: #fff;
}
.bgM {
    background: #fff;
    height:auto;
}
.pageHeader {
    padding: 20px 0px;
    position: fixed;
    top: 0;
    z-index: 9;
    background: #fff;
    width: 100%;

    .left {
        a {
            color:#000;
        }
        .back {
            font-size:22px;
        }
    }

    .center {

        h5 {
            margin:0;
        }
    }
    
}


.bookBtn {
    background-color: var(--Main1);
    width: 100%;
    display: block;
    color: #fff !important;
    text-decoration: none;
    text-align: center;
    padding: 15px 10px;
    border-radius: 10px;
}

.formBox {
    margin-top: 20px;
    text-align: left;

    label {
        margin-bottom: 8px;
    }

    :global(.form-control) {
        border: none;
        padding:12px 15px;
        border-radius: 10px !important;
        border: 1px solid #000;

        &:focus {
            box-shadow:none;
        }
    }
}

:global(.App) , :global(#root) {
   height:100%;
}

.loginTitle {

    h5 {
        font-size: 28px;
        padding:60px 0px 0px 0px;
        margin-bottom: 5px;
    }
    p {
        margin:0;
        font-size:18px;
    }
}



.dontAccount {
    
    a {
    color: var(--Main1);
    text-decoration:none;
    }
}

.password {

    .icon {
        position: absolute;
        right: 0px;
        top: 50%;
        transform: translate(-50%, -50%);
        font-size: 22px;
        cursor: pointer;
        z-index:99;
    }
}

.mds {
    list-style:none;
    margin:0;
    padding:0;
    text-align: left;
    margin-top:15px;

    li {
        margin:2px 0px;
        font-size:14px;
    }

    .checkbox {
        font-size:14px;
        margin-top:10px;

        a {
            text-decoration:underline;
            color:#000;
        }

    }
}


.checkbox {
	position: relative;
	cursor: pointer;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	padding-left: 25px;
  
	&:hover input ~ .checkmark {
	    background-color: #f3f1f1;
		border: 1px solid #d8d8d8;
	}
  
    input {
		opacity: 0;
		margin-right:10px;
		position:absolute;
	  
	    &:checked ~ .checkmark {
		  background-color: var(--Main1);
		  border: 1px solid var(--Main1);
		}
		&:checked ~ .checkmark:after {
		  display: block;
		}
	}
	.checkmark {
		position: absolute;
		top: 3px;
		left: 0;
		height: 18px;
		width: 18px;
		background-color: #fff;
		border-radius: 3px;
        border: 1px solid #d8d8d8;

	    &:after {
			content: "";
			position: absolute;
			display: none;
		    left: 6px;
			top: 2px;
			width: 5px;
			height: 10px;
			border: solid white;
			border-width: 0 2px 2px 0;
			-webkit-transform: rotate(45deg);
		    -ms-transform: rotate(45deg);
			transform: rotate(45deg);
		}
    }	
}