body {
    background: #f0f4f7;
}
.bgM {
    background: #f0f4f7;
}
.pageHeader {
    padding: 15px 0px;
    position: fixed;
    top: 0;
    z-index: 9;
    background: #f0f4f7;
    width: 100%;

    .left {
        a {
            color:#000;
        }
        .back {
            font-size:22px;
        }
    }

    .center {

        h5 {
            margin:0;
        }
    }
    
}

.docotorCard {
    background: #f5f6f8;
    padding: 20px 20px;
    border-radius: 20px;
    position: relative;
    box-shadow: 0 0px 6px 1px #eee9e9;

    .left {

        h5 {
            font-size: 20px;
            font-weight: 600;
        }
        p {
            margin-bottom: 3px;
            color: #818181;
            font-size: 15px;
        }
    }

    .right {
        text-align: center;

        img {
            width: 110px;
            height: 110px;
            border-radius: 50%;
        }
    }

    .boxP {
        background-color: #fff;
        padding: 20px 10px;
        text-align: center;
        border-radius:20px;
        margin-top: 20px;

        p {
            margin-bottom: 3px;
            font-size: 18px;
            color: #818181;
        }
        h5 {
            margin: 0;
            font-size: 22px;
        }
    }

    p {
        
        color: #818181;
    }
}


.bookBtn {
    background-color: #2758e3;
    width: 100%;
    display: block;
    color: #fff !important;
    text-decoration: none;
    text-align: center;
    padding: 15px 10px;
    border-radius: 10px;
}