body {
    background: #f0f4f7;
}
.bgM {
    background: #f0f4f7;
}
.pageHeader {
    padding: 15px 0px;
    position: fixed;
    top: 0;
    z-index: 9;
    background: #f0f4f7;
    width: 100%;

    .left {
        a {
            color:#000;
        }
        .img {
            height:22px;
        }
    }

    .center {

        h5 {
            margin:0;
        }
    }
    
}

.rangeBox {

    p {
        text-align: center;
        color: #818181;
    }

    .lockImg {
        height: 100px;
        margin:15px 0px 20px 0px;
    }


       
    h5 {
        text-align: center;
        margin: 10px 0px 5px 0px;
        font-size: 24px;
    }

    }



.bookBtn {
    background-color: var(--Main1);
    width: 100%;
    display: block;
    color: #fff !important;
    text-decoration: none;
    text-align: center;
    padding: 15px 10px;
    border-radius: 10px;
}

.selectCountry {
    text-align:center;
    margin: 10px 0px 10px 0px;


    ul {
        list-style:none;
        margin:0;
        padding:0;
        text-align:left;
        margin-top: 10px;

        li {
            margin-bottom:15px;

            h5 {
                font-size:20px;
            }

            a {
               color:#5c5c5c;
               font-weight: 500;
            }
        }
    }

}


.docotorCard {
    background: #fff;
    padding: 20px 20px;
    border-radius: 20px;
    position: relative;
    box-shadow: 0 0px 6px 1px #eee9e9;
    margin-bottom:20px;

    &.docotorCate {
        background-color: #f5f6f8;

        h3 {
            font-size: 30px;
            font-weight: bold;
            margin-bottom: 30px;
        }

        ul {
            list-style: none;
            margin: 0;
            padding: 0;
            display: flex;
            overflow: hidden;
            overflow-y: hidden;
            overflow-x: auto;
            padding-bottom: 15px;

            li {

                a {
                    width: 50px;
                    height: 50px;
                    display: block;
                    box-shadow: 0 0px 6px 1px #eee9e9;
                    border-radius: 50px;
                    text-align: center;
                    padding: 9px 0px;
                    margin-right: 12px;
                    background-color: #fff;

                    img {
                        height: 25px;
                    }
                }
            }
        }
    }

    .left {

        h5 {
            font-size: 20px;
            font-weight: 600;
        }
        p {
            margin-bottom: 3px;
            color: #818181;
            font-size: 15px;
        }
    }

    .right {
        text-align: center;

        img {
            width: 110px;
            height: 110px;
            border-radius: 50%;
        }
    }

    .boxP {
        background-color: #fff;
        border-radius:20px;
        margin-top: 20px;

        h5 {
            margin: 0;
            font-size: 30px;
            font-weight: bold;
        }

        :global(.btn) {
            padding: 10px 15px;
            font-size: 14px;
            background: #ececec;
            border-radius: 8px;
            font-weight: 500;

            &:focus {
                box-shadow: none;
            }
        }
    }

    p {
        
        color: #818181;
    }
}


