.footerBottombar {
    position: fixed;
    bottom: 0;
    background: #fff;
    width: 100%;
    padding: 12px 0px;
    border-top: 1px solid #d4d4d4;

    .menu {
        text-align: center;
        text-decoration: none;
        color: #808080;
        display: block;

        h6 {
            font-size: 14px;
            font-weight: normal;
            margin: 0;
        }
        .icon {
            color:var(--Main1);
            margin-bottom: 8px;
            font-size: 20px;
        }
    }
}