body {
    background: #f0f4f7;
}
.bgM {
    background: #f0f4f7;
}


.pageHeader {
    padding: 15px 0px;
    position: fixed;
    top: 0;
    z-index: 9;
    background: #f0f4f7;
    width: 100%;

    .left {
        a {
            color:#000;
        }
        .back {
            font-size:22px;
        }
    }

    .center {

        .img {
            width: 130px;
        }
    }
    .right {

        .menu {
            font-size: 28px;
        }

    }
}

.docotorCard {
    background: #fff;
    padding: 20px 20px;
    border-radius: 20px;
    position: relative;
    box-shadow: 0 0px 6px 1px #eee9e9;
    margin-top:20px;

    &.docotorCate {
        background-color: #f5f6f8;

        h3 {
            font-size: 30px;
            font-weight: bold;
            margin-bottom: 30px;
        }

        ul {
            list-style: none;
            margin: 0;
            padding: 0;
            display: flex;
            overflow: hidden;
            overflow-y: hidden;
            overflow-x: auto;
            padding-bottom: 15px;

            li {

                a {
                    width: 50px;
                    height: 50px;
                    display: block;
                    box-shadow: 0 0px 6px 1px #eee9e9;
                    border-radius: 50px;
                    text-align: center;
                    padding: 9px 0px;
                    margin-right: 12px;
                    background-color: #fff;

                    img {
                        height: 25px;
                    }
                }
            }
        }
    }

    .left {

        h5 {
            font-size: 20px;
            font-weight: 600;
        }
        p {
            margin-bottom: 3px;
            color: #818181;
            font-size: 15px;
        }
    }

    .right {
        text-align: center;

        img {
            width: 110px;
            height: 110px;
            border-radius: 50%;
        }
    }

    .boxP {
        background-color: #fff;
        border-radius:20px;
        margin-top: 20px;

        h5 {
            margin: 0;
            font-size: 30px;
            font-weight: bold;
        }

        :global(.btn) {
            padding: 10px 15px;
            font-size: 14px;
            background: #ececec;
            border-radius: 8px;
            font-weight: 500;

            &:focus {
                box-shadow: none;
            }
        }
    }

    p {
        
        color: #818181;
    }
}



.bookBtn {
    background-color: var(--Main1);
    width: 100%;
    display: block;
    color: #fff !important;
    text-decoration: none;
    text-align: center;
    padding: 15px 10px;
    border-radius: 10px;
}




.sideBar {
    display: inline-block;

    .sideBarBg {
        background: #4f4f4f70;
        width: 100%;
        height: 100%;
        position: fixed;
        left: -100%;
        top: 0px;
        z-index: 99;
        opacity:0;
    }
    .sideBarMenuList {
        background: #fff;
        width: 250px;
        height: 100%;
        position: fixed;
        left: -100%;
        z-index: 99;
        top: 0;
        transition: 0.5s;
    }

    &.sidebarShow {


        .sideBarMenuList {
            left: 0px;
        }

        .sideBarBg {
            opacity:1;
            left: 0;
        }
    }
}



@media only screen and (max-width: 5520px) and (min-width: 768px) {

    

    .sideBar {
    
        .sideBarBg {
            max-width: 400px;
            transform: translate(-50%, -0%);
            left: -100%;
        }
        .sideBarMenuList {
            transform: translate(-50%, -0%);
            margin-left: -75px;
            transition: 0.01s;
        }
    
        &.sidebarShow {
    
            .sideBarMenuList {
                left: 50% !important;
            }
            .sideBarBg {
                left: 50%;
            }
        }
    }
}