

    .sideBarMenu {
        width: 100%;
        height: 100%;
        position: relative;

        .logo {
            height: 30px;
            margin: 30px 0px 20px 0px;
        }

        ul {
            list-style: none;
            margin:0;
            padding: 0;

            li {

                .menuLink {
                    display:flex;
                    align-items: center;
                    color:#000;
                    font-weight: 500;
                    padding: 10px 15px;
                    text-decoration: none;

                    span {
                        color: #99a6b6;
                        margin-right: 10px;
                        width: 25px;
                        text-align: center;
                        font-size: 19px;
                        position: relative;
                        top: -2px;

                        svg {
                            
                        }
                    }

                    p {
                        margin:0;

                        span {
                            width: 100%;
                            display: block;
                            text-align: left;
                            font-size: 16px;
                            color: #000;
                        }

                        strong {
                            font-size: 12px;
                            font-weight: normal;
                            color: #78828d;
                        }
                    }
                }
            }
        }
    }