body {
    background: #fff;
}
.bgM {
    background: #fff;
}
.pageHeader {
    padding: 20px 0px;
    position: fixed;
    top: 0;
    z-index: 9;
    background: #fff;
    width: 100%;

    .left {
        a {
            color:#000;
        }
        .back {
            font-size:22px;
        }
    }

    .center {

        h5 {
            margin:0;
        }
    }
    
}


.bookBtn {
    background-color: var(--Main1);
    width: 100%;
    display: block;
    color: #fff !important;
    text-decoration: none;
    text-align: center;
    padding: 15px 10px;
    border-radius: 10px;
}

.formBox {
    margin-top: 20px;
    text-align: left;

    label {
        margin-bottom: 8px;
    }

    :global(.form-control) {
        border: none;
        padding:12px 15px;
        border-radius: 10px !important;
        border: 1px solid #000;

        &:focus {
            box-shadow:none;
        }
    }
}

:global(.App) , :global(#root) {
   height:100%;
}

.loginTitle {

    h5 {
        font-size: 28px;
        padding:60px 0px 20px 0px;
    }
}

.forgotpassword {
    text-decoration: none;
    color: var(--Main1);
    display: block;
}

.dontAccount {
    
    a {
    color: var(--Main1);
    text-decoration:none;
    }
}

.password {

    .icon {
        position: absolute;
        right: 0px;
        top: 50%;
        transform: translate(-50%, -50%);
        font-size: 22px;
        cursor: pointer;
        z-index:99;
    }
}