.docotorCard {
    background: #fff;
    padding: 20px 20px;
    border-radius: 20px;
    position: relative;
    box-shadow: 0 0px 6px 1px #eee9e9;
    margin-top:20px;
    background-color: #f5f6f8;


    h3 {
        font-size: 22px;
        margin-bottom: 0px;
        line-height: 33px;
        font-weight: bold;
        color: #424242;

        span {
            text-decoration: underline;
        }
    }
}

.formBoxMain {
    background: #fff;
    padding: 20px 20px;
    border-radius: 20px;
    position: relative;
    box-shadow: 0 0px 6px 1px #eee9e9;
    margin-top:20px;
    background-color: #f5f6f8;

    
    h3 {
        font-size: 18px;
        color: #424242;
        font-weight: bold;
        margin-bottom:15px;
    }

    p {
        font-size:14px;
        margin-bottom:25px;
    }

    .formBoxMainBody {
        background-color: #fff;
        padding: 20px 20px;
        border-radius: 20px;

        :global(.form-control) {
            border: 1px solid #f0f4f7;
            padding: 10px 12px;
            border-radius: 10px;
            background-color: #f0f4f7;
            font-size: 14px;

            &:focus {
                box-shadow:none;
            }
        }

        :global(.form-label) {
            font-weight: 500;
            font-size: 14px;
            margin-bottom: 5px;
        }
    }
}



.checkbox {
	position: relative;
	cursor: pointer;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	padding-left: 25px;
    font-size:14px;
    margin:8px 0px;
  
	&:hover input ~ .checkmark {
	    background-color: #fff;
        border: 2px solid var(--Main1);
	}
  
    input {
		opacity: 0;
		margin-right:10px;
		position:absolute;
	  
	    &:checked ~ .checkmark {
		  background-color: var(--Main1);
		  border: 2px solid var(--Main1);
		}
		&:checked ~ .checkmark:after {
		  display: block;
		}
	}
	.checkmark {
		position: absolute;
		top: 2px;
		left: 0;
		height: 18px;
		width: 18px;
		background-color: #fff;
		border-radius: 5px;
        border: 2px solid var(--Main1);

	    &:after {
			content: "";
			position: absolute;
			display: none;
		    left: 5px;
			top: 1px;
			width: 5px;
			height: 10px;
			border: solid white;
			border-width: 0 2px 2px 0;
			-webkit-transform: rotate(45deg);
		    -ms-transform: rotate(45deg);
			transform: rotate(45deg);
		}
    }	
}



.bookBtn {
    background-color: var(--Main1);
    width: 100%;
    display: block;
    color: #fff !important;
    text-decoration: none;
    text-align: center;
    padding: 15px 10px;
    border-radius: 10px;
}


.popupListing2 {
    position: fixed;
    z-index: 99;
    top: 100%;
    background: transparent;
    height: 100%;
    padding: 15px 15px;
    overflow: hidden;
    height: calc(100vh - 0px);
    overflow-x: hidden;
    overflow-y: auto;
    transition: 0.2s;

    &.popupShow {
       top: 0;
    }

    .popupListingBody {
        box-shadow: 0 0px 6px 1px #c0c0c0;
        padding:25px 20px;
        border-radius: 15px;
        background: #fff;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 90%;
        text-align: center;


        h5 {
            text-align: center;
            margin: 0px 0px 15px 0px;
            font-size: 18px;
       }
       

        :global(.btn) {
            background-color: var(--Main1);
            color: #fff;
            border-radius: 10px;
            padding: 8px 15px;
            min-width: 100px;
        }
    }
}