

.pageHeader {
    padding: 15px 0px;
    position: fixed;
    top: 0;
    z-index: 9;
    background: #fff;
    width: 100%;

    .center {

        .img {
            width: 130px;
        }
    }
    .right {

        .menu {
            font-size: 28px;
        }

    }
}

.docSl {
    position: relative;

    .docSlBg {
        background-image: linear-gradient( var(--Main1), var(--Main2));
        height: 180px;
        border-radius: 0px 0px 30px 30px;
    }
    .docRow {
        position: absolute;
        top: 30px;
        width: 100%;

        .docCard {
            border-radius: 15px;
            overflow: hidden;
            text-decoration: none;
            display: block;
            color:#000;
    
            .docCardImg {
    
                img {
                    width: 100%;
                    height: 100px;
                    object-fit: cover;
                }
            }
            .docCardbody {
                text-align: center;
                background-image: linear-gradient( var(--Main1), var(--Main2));
                padding: 10px 10px;


                h5 {
                    margin-bottom: 4px;
                    font-size: 15px;
                    font-weight: 600;
                }
                p {
                    margin: 0;
                    font-weight: 500;
                    line-height: 17px;
                    font-size: 12px;
                }
            }
        }

    }
}

.specRow {
    margin: 0px 8px !important;

    :global(.col-3) {
        padding: 0px;
       
    }

}

.specBox {
    text-align: center;
    margin: 0 auto;
    margin-bottom: 20px;
    text-decoration: none;
    color: #333;
    display: block;

    .specBoxImg {
        box-shadow:0 0px 6px 1px #eee9e9;
        border-radius: 8px;
        text-align: center;
        padding: 19px 10px;
        height: 75px;
        width: 75px;    
        margin: 0 auto;

        img {
            height: 38px;
        }
    }
    .specBoxBody {
        margin-top: 10px;

        h6 {
            font-size: 12px;
            margin-bottom: 0;
            font-weight: normal;
        }
        p {
            margin: 0;
            font-size: 12px;
            color: #818181;
        }
    }
}


.consultBtn {
    margin-top: 20px;

    :global(.btn) {
        background-color: var(--Main2);
        color: #fff;
        padding: 7px 15px;
        border-radius: 8px;
        font-size: 14px;
    }
}



.sideBar {
    display: inline-block;

    .sideBarBg {
        background: #4f4f4f70;
        width: 100%;
        height: 100%;
        position: fixed;
        left: -100%;
        top: 0px;
        z-index: 99;
        opacity:0;
    }
    .sideBarMenuList {
        background: #fff;
        width: 250px;
        height: 100%;
        position: fixed;
        left: -100%;
        z-index: 99;
        top: 0;
        transition: 0.5s;
    }

    &.sidebarShow {


        .sideBarMenuList {
            left: 0px;
        }

        .sideBarBg {
            opacity:1;
            left: 0;
        }
    }
}



@media only screen and (max-width: 5520px) and (min-width: 768px) {

    

    .sideBar {
    
        .sideBarBg {
            max-width: 400px;
            transform: translate(-50%, -0%);
            left: -100%;
        }
        .sideBarMenuList {
            transform: translate(-50%, -0%);
            margin-left: -75px;
            transition: 0.01s;
        }
    
        &.sidebarShow {
    
            .sideBarMenuList {
                left: 50% !important;
            }
            .sideBarBg {
                left: 50%;
            }
        }
    }
}