body {
    background: #f0f4f7;
}
.bgM {
    background: #f0f4f7;
}
.pageHeader {
    padding: 15px 0px;
    position: fixed;
    top: 0;
    z-index: 9;
    background: #f0f4f7;
    width: 100%;

    .left {
        a {
            color:#000;
        }
        .back {
            font-size:22px;
        }
    }

    .center {

        h5 {
            margin:0;
        }
    }
    
}


.bookBtn {
    background-color: var(--Main1);
    width: 100%;
    display: block;
    color: #fff !important;
    text-decoration: none;
    text-align: center;
    padding: 15px 10px;
    border-radius: 10px;
}

.profileUpload {
    position: relative;
    display: inline-block;
    width: 100px;
    height: 100px;
    border-radius: 50px;
    overflow: hidden;
    margin: 0 auto;

    img {
        width: 100%;
        height: 100%;
        border-radius: 50px;
        object-fit: cover;
    }
    input[type="file"] {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        cursor: pointer;
        opacity:0;
    }
}

.uploadBtn {
    position: relative;
    overflow: hidden;
    border: none;
    padding: 0;
    font-weight: 500;
    display: block;
    text-align: center;
    margin: 0 auto;
    margin-top: 10px;

    input[type="file"] {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        cursor: pointer;
        opacity:0;
    }
    
}

.formBox {
    background-color: #fff;
    text-align: left;
    padding:5px 15px 10px 15px;
    border-radius: 10px;
    margin-top: 20px;

    label {
        font-size: 10px;
        margin-bottom: 0px;
    }

    :global(.form-control) {
        border:none;
        padding: 0px 0px;
        font-size: 18px;

        &:focus {
            box-shadow:none;
        }
    }
}