body {
    background: #f0f4f7;
}
.bgM {
    background: #f0f4f7;
}
.pageHeader {
    padding: 15px 0px;
    position: fixed;
    top: 0;
    z-index: 9;
    background: #f0f4f7;
    width: 100%;

    .left {
        a {
            color:#000;
        }
        .back {
            font-size:22px;
        }
    }

    .center {

        h5 {
            margin:0;
        }
    }
    
}

.rangeBox {

    p {
        text-align: center;
        color: #818181;
    }


        .closeIcon {
            position: absolute;
            right: 15px;
            top: 15px;
            font-size: 22px;
            cursor: pointer;
        }

        h5 {
            text-align: center;
            margin: 10px 0px 5px 0px;
            font-size: 20px;
        }

        ul {
            list-style: none;
            margin: 0;
            padding: 0;

            li {

                button {
                    width: 100%;
                    border: 1px solid #f0f0f0;
                    background: #fff;
                    margin: 10px 0px;
                    padding: 12px 15px;
                    border-radius: 15px;
                    text-align: left;
                    color:#818181;
                    display: flex;
                    align-items: center;

                    .icon {
                        color: #7ab5e7;
                        font-size: 20px;
                        margin-right: 5px;
                    }

                    &:focus, &.active {
                        border: 1px solid #374061;
                        background-color: #374061;
                        color:#fff;
                    }
                }
            }
        }
}



.bookBtn {
    background-color: var(--Main1);
    width: 100%;
    display: block;
    color: #fff !important;
    text-decoration: none;
    text-align: center;
    padding: 15px 10px;
    border-radius: 10px;
}