body {
    background: #f0f4f7;
}
.bgM {
    background: #f0f4f7;
}
.pageHeader {
    padding: 15px 0px;
    position: fixed;
    top: 0;
    z-index: 9;
    background: #f0f4f7;
    width: 100%;

    .left {
        a {
            color:#000;
        }
        .back {
            font-size:22px;
        }
    }

    .center {

        h5 {
            margin:0;
        }
    }
    
}

.rangeBox {

    p {
        text-align: center;
        color: #818181;
    }

    .lockImg {
        height: 100px;
        margin:15px 0px 20px 0px;
    }


       
        h5 {
            text-align: center;
            margin: 10px 0px 5px 0px;
            font-size: 20px;
        }

    }



.bookBtn {
    background-color: var(--Main1);
    width: 100%;
    display: block;
    color: #fff !important;
    text-decoration: none;
    text-align: center;
    padding: 15px 10px;
    border-radius: 10px;
}

.selectCountry {
    text-align:center;
    margin: 10px 0px 30px 0px;

   .flagIcon {
