body {
    background: #f0f4f7;
}
.bgM {
    background: #f0f4f7;
}


.pageHeader {
    padding: 15px 0px;
    position: fixed;
    top: 0;
    z-index: 9;
    background: #f0f4f7;
    width: 100%;

    .left {
        a {
            color:#000;
        }
        .back {
            font-size:22px;
        }
    }

    .center {

        .img {
            width: 130px;
        }
    }
    .right {

        .menu {
            font-size: 28px;
        }

    }
}
.consultTitle {
    margin-top: 20px;
    align-items:center;

    .left {

        h5 {
            font-size:24px;
        }
    }
    .right {
        text-align:right;

        .icon {
            font-size: 36px;
            color:#068307;
        }
    }
}
.consultCardMain {
    margin-top:20px;
    box-shadow: 0 0px 6px 1px #eee9e9;
    background: #f5f6f8;
    border-radius: 20px;
    padding: 15px 15px;

    h3 {
        font-size: 20px;
        margin-bottom: 15px;
    }

.consultCard {
    background: #fff;
    padding: 20px 20px;
    border-radius: 20px;
    position: relative;
    box-shadow: 0 0px 6px 1px #eee9e9;

    &.docotorCate {
        background-color: #f5f6f8;

        h3 {
            font-size: 30px;
            font-weight: bold;
            margin-bottom: 30px;
        }

        ul {
            list-style: none;
            margin: 0;
            padding: 0;
            display: flex;
            overflow: hidden;
            overflow-y: hidden;
            overflow-x: auto;
            padding-bottom: 15px;

            li {

                a {
                    width: 50px;
                    height: 50px;
                    display: block;
                    box-shadow: 0 0px 6px 1px #eee9e9;
                    border-radius: 50px;
                    text-align: center;
                    padding: 9px 0px;
                    margin-right: 12px;
                    background-color: #fff;

                    img {
                        height: 25px;
                    }
                }
            }
        }
    }

    .left {
        padding-right:0px;

        .colorBg {
            background-color: #d5982a;
            text-align: center;
            color: #fff;
            border-radius: 50px;
            padding: 15px 8px;
            width: 50px;

            h5 {
                font-size: 20px;
                font-weight: 600;
            }
            p {
                margin-bottom: 0px;
                color: #fff;
                font-size: 20px;
            }

        }
    }

    .center {
        padding-left: 0px;
        padding-top: 25px;
        
        p {
            color: #212121;
            margin-bottom: 10px;
            font-weight: 500;
        }

        :global(.btn) {
            background-color: #c70039;
            color: #fff;
            padding: 8px 10px;
            font-size: 14px;
            border-radius: 8px;
            min-width: 130px;
            margin-top: 10px;
        }
    }

    .right {
        text-align: center;
        padding-left: 0px;

        img {
            width: 67px;
            height: 67px;
            border-radius: 50%;
        }
    }

    
}

}




.sideBar {
    display: inline-block;

    .sideBarBg {
        background: #4f4f4f70;
        width: 100%;
        height: 100%;
        position: fixed;
        left: -100%;
        top: 0px;
        z-index: 99;
        opacity:0;
    }
    .sideBarMenuList {
        background: #fff;
        width: 250px;
        height: 100%;
        position: fixed;
        left: -100%;
        z-index: 99;
        top: 0;
        transition: 0.5s;
    }

    &.sidebarShow {


        .sideBarMenuList {
            left: 0px;
        }

        .sideBarBg {
            opacity:1;
            left: 0;
        }
    }
}



@media only screen and (max-width: 5520px) and (min-width: 768px) {

    

    .sideBar {
    
        .sideBarBg {
            max-width: 400px;
            transform: translate(-50%, -0%);
            left: -100%;
        }
        .sideBarMenuList {
            transform: translate(-50%, -0%);
            margin-left: -75px;
            transition: 0.01s;
        }
    
        &.sidebarShow {
    
            .sideBarMenuList {
                left: 50% !important;
            }
            .sideBarBg {
                left: 50%;
            }
        }
    }
}