body {
    background: #f0f4f7;
}
.bgM {
    background: #f0f4f7;
}
.pageHeader {
    padding: 15px 0px;
    position: fixed;
    top: 0;
    z-index: 9;
    background: #f0f4f7;
    width: 100%;

    .left {
        a {
            color:#000;
        }
        .back {
            font-size:22px;
        }
    }

    .center {

        h5 {
            margin:0;
        }
    }
    .right {

        :global(.btn) {
            border: none;
            background-color: var(--Main1);
            padding: 7px 5px;
            font-size: 12px;
            color: #fff;
            line-height: 14px;
            border-radius: 8px;

            &:focus {
                box-shadow:none;
            }
        }

    }
}

.filterBox {
    padding: 12px 15px;
    background: #fff;
    border-radius: 10px;
    margin-top: 15px;
    display: flex;
    align-items: center;
    cursor: pointer;

    .icon {
        color:#5f5e5e;
        margin-right: 10px;
    }
    .filter {
        color:#5f5e5e;
        margin-right: 10px;
    }
    strong {
        font-weight: 600;
    }
}


.docotorCard {
    background: #f5f6f8;
    padding: 55px 15px 25px 15px;
    border-radius: 20px;
    position: relative;
    margin-top: 50px;
    box-shadow: 0 0px 6px 1px #eee9e9;

    .left {

        h5 {
            font-size: 16px;
            margin-bottom: 8px;
        }
        p {
            margin: 0px;
            font-size: 14px;
            color:#818181;
        }
    }
    .center {

        img {
            width: 80px;
            height: 80px;
            border-radius: 50%;
            position: absolute;
            top: 10px;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }
    .feesBox {
        float: right;
    }
    .right {

        p {
            font-size: 14px;
            color:#818181;
            margin-bottom: 8px;

            strong {
                font-size: 14px;
            }
        }
    }

    .request {

        :global(.btn) {
            padding: 8px 15px;
            background-color: #fff;
            font-size: 12px;
            border-radius: 10px;
            font-weight: 500;
            margin-top: 5px;

            &:focus {
                box-shadow: none;
            }
        }
    }
}


.popupListing {
    position: fixed;
    z-index: 99;
    top: 100%;
    background: #f0f4f7;
    height: 100%;
    padding: 15px 15px;
    overflow: hidden;
    height: calc(100vh - 0px);
    overflow-x: hidden;
    overflow-y: auto;
    transition: 0.2s;

    &.popupShow {
       top: 0;
    }

    .popupListingBody {
        box-shadow: 0 0px 6px 1px #c0c0c0;
        padding: 15px 15px;
        border-radius: 15px;
        position: relative;

        .closeIcon {
            position: absolute;
            right: 15px;
            top: 15px;
            font-size: 22px;
            cursor: pointer;
        }

        h5 {
            text-align: center;
            margin: 40px 0px 15px 0px;
            font-size: 20px;
        }

        ul {
            list-style: none;
            margin: 0;
            padding: 0;

            li {

                button {
                    width: 100%;
                    border: 1px solid #f0f0f0;
                    background: #fff;
                    margin: 5px 0px;
                    padding: 8px 15px;
                    border-radius: 10px;
                    text-align: left;
                    color:#818181;
                }
            }
        }
    }
}



.popupListing2 {
    position: fixed;
    z-index: 99;
    top: 100%;
    background: transparent;
    height: 100%;
    padding: 15px 15px;
    overflow: hidden;
    height: calc(100vh - 0px);
    overflow-x: hidden;
    overflow-y: auto;
    transition: 0.2s;

    &.popupShow {
       top: 0;
    }

    .popupListingBody {
        box-shadow: 0 0px 6px 1px #c0c0c0;
        padding:15px 15px 25px 15px;
        border-radius: 15px;
        background: #fff;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 90%;
        text-align: center;

        .closeIcon {
            position: absolute;
            right: 15px;
            top: 15px;
            font-size: 22px;
            cursor: pointer;
        }

        h5 {
            text-align: center;
            margin: 40px 0px 15px 0px;
            font-size: 20px;
       }
       
       h6 {
        text-align: center;
        margin: 0px 0px 15px 0px;
        font-size: 20px;
        }

        :global(.btn) {
            background-color: var(--Main1);
            color: #fff;
            border-radius: 10px;
            padding: 8px 15px;
        }
    }
}