@import "variables.module.scss"; 


:global(.App) {
    text-align: left;
}


html {
    background-color: #f1f1f1;
    height: 100%;
    font-family: 'Poppins', sans-serif;
}
body {
    max-width: 400px;
    margin: 0 auto;
    position: relative;
    background-color: #fff;
    height: 100%;
}
.fixing {
    width: 400px !important;
}

.pageWrapper {
    margin: 0 auto;
    background: #fff;
}


.title {

    .left {

        h5 {
            font-size: 24px;
            font-weight: 600;
        }
        h6 {
            font-size: 24px;
            font-weight: 600;
        }
    }
}

.bottomSpace {
    padding-bottom: 100px;
}
.spaceTop {
    margin-top:63px;
}


/*=======================================================
//////////////RESPONSIVE MEDIA QUERY START///////////////
=======================================================*/
@media (max-width: 767px) {


 html {
    background-color: transparent;
}
.fixing {
    width: 100% !important;
}

}