body {
    background: #f0f4f7;
}
.bgM {
    background: #f0f4f7;
}
.pageHeader {
    padding: 15px 0px;
    position: fixed;
    top: 0;
    z-index: 9;
    background: #f0f4f7;
    width: 100%;

    .left {
        a {
            color:#000;
        }
        .back {
            font-size:22px;
        }
    }

    .center {

        h5 {
            margin:0;
        }
    }
    
}

.rangeBox {

    p {
        text-align: center;
        color: #818181;
    }

    .lockImg {
        height: 100px;
        margin:15px 0px 20px 0px;
    }


       
    h5 {
        text-align: center;
        margin: 10px 0px 5px 0px;
        font-size: 20px;
    }

    }



.bookBtn {
    background-color: var(--Main1);
    width: 100%;
    display: block;
    color: #fff !important;
    text-decoration: none;
    text-align: center;
    padding: 15px 10px;
    border-radius: 10px;
}

.selectCountry {
    text-align:center;
    margin: 10px 0px 10px 0px;

    h5 {
        text-align: center;
        margin: 10px 0px 10px 0px;
        font-size: 20px;
    }

    .dark {
        list-style:none;
        margin:0;
        padding:0;

        li {
            display: inline-block;
            background: #374061;
            color: #fff;
            padding: 8px 10px;
            line-height: 0px;
            border-radius: 50px;
            margin: 5px 3px;
            font-size: 12px;

            .close {
                margin-left:5px;
                cursor:pointer;
                font-size: 14px;
            }
        }
    }
    .light {
        list-style:none;
        margin:0;
        padding:0;

        li {
            display: inline-block;
            background: #fff;
            color: #000;
            padding: 8px 10px;
            line-height: 0px;
            border-radius: 50px;
            margin: 5px 3px;
            font-size: 12px;

            .close {
                margin-left:5px;
                cursor:pointer;
                font-size: 14px;
            }
        }
    }
}

.searchMain {
    border: 1px solid #ececec;
    padding: 5px 10px;
    background: #fff;
    border-radius: 8px;
    margin: 15px 0px;

    :global(.input-group-text) {
        padding: 0px 0px;
        background: #fff;
        font-size: 23px;
        border: none;
    }
    :global(.form-control) {
       border:none;

       &:focus {
           box-shadow:none;
       }
    }
}